import { json, useNavigate, useParams } from "react-router-dom";
import * as React from "react";
import axios from "axios";
import Editor from "@monaco-editor/react";
import ContainerLogs from "./ContainerLogs";
import { toast } from 'react-toastify';


export default function Domain() {
  const { domain } = useParams();

  //Get Hostname from url 
  var hostname = window.location.hostname;

  const [isLoading, setIsLoading] = React.useState(false);
  const [data, setData] = React.useState([]);
  const [container_id, setContainerId] = React.useState("");
  const [port, setPort] = React.useState("");

  //Get IP Address
  const [ip, setIP] = React.useState("");
  const getIP = async () => {
    await axios.get(`${process.env.REACT_APP_BACKEND_URL}/v1/system/get-ip`).then((res) => {
      setIP(res.data);
    }).catch((e) => {
      console.log(e);
    });
  };

  //Get Domain Data
  async function getDomain() {
    setIsLoading(true);
    await axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/v1/web/get-web/${domain}`)
      .then((res) => {
        setIsLoading(false);
        setData(res.data);
        setContainerId(res.data[0].container_id);
        setPort(res.data[0].bind_port);
        console.log(res.data.host_path)
      })
      .catch((e) => {
        setIsLoading(false);
      });
  }

  //Container details 
  const [container_details, setContainer] = React.useState([]);
  const [container_status, setContainerStatus] = React.useState("");

  async function getContainer() {
    await axios.get(`${process.env.REACT_APP_BACKEND_URL}/v1/docker/container-details/${container_id}`)
      .then((res) => {
        setContainer(res.data);
        setContainerStatus(res.data.container.State.Status);
        console.log(res.data)
      })
      .catch((e) => {
        console.log(e)
      });
  }

  const navigate = useNavigate();


  //Delete Website
  const [isDeleting, setIsDeleting] = React.useState(false);
  const deleteDomain = async () => {
    setIsDeleting(true);
    await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/v1/web/delete-web`, {
        // Domain: domain,
        Name: domain,
      })
      .then((res) => {
        setIsDeleting(false);
        navigate("/ui/admin/web/create");
        toast.success(res.data.message);
      })
      .catch((e) => {
        setIsDeleting(false);
        toast.error(e.response.data.message);
      });
  };


  //Tabs 
  const [tab, setTab] = React.useState("details");

  const [isActionInProgress, setIsActionInProgress] = React.useState(false);

  //Stop Container
  const stopContainer = async () => {
    setIsActionInProgress(true);
    await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/v1/docker/stop-container`, {
        //ID
        ID: data[0].container_id,
      })
      .then((res) => {
        setIsActionInProgress(false);
        getContainer();
        toast(res.data.message, {
          type: "success",
        });
      })
      .catch((e) => {
        setIsActionInProgress(false);
        toast(e.response.data.message, {
          type: "error",
        });
      });
  };

  //Start Website
  const startContainer = async () => {
    setIsActionInProgress(true);
    await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/v1/docker/start-container`, {
        //ID
        ID: data[0].container_id,
      })
      .then((res) => {
        setIsActionInProgress(false);
        getContainer();
        toast(res.data.message, {
          type: "success",
        });
      })
      .catch((e) => {
        setIsActionInProgress(false);
        toast(e.response.data.message, {
          type: "error",
        });
      });
  };


  //Restart 
  const [isRestarting, setIsRestarting] = React.useState(false);
  const restartContainer = async () => {
    setIsRestarting(true);
    getContainer();
    await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/v1/docker/restart-container`, {
        // Domain: domain,
        Id: data[0].container_id,
      })
      .then((res) => {
        setIsRestarting(false);
        getContainer();
        toast(res.data.message, {
          type: "success",
        });
      })
      .catch((e) => {
        setIsRestarting(false);
        toast(e.response.data.message, {
          type: "error",
        });
        getContainer();
      });
  };


  //Restart 
  const [isRebuilding, setIsRebuilding] = React.useState(false);
  const reBuild = async () => {
    setIsRebuilding(true);
    getContainer();
    await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/v1/web/rebuild-web`, {
        // Domain: domain,
        Name: data[0].name,
      })
      .then((res) => {
        setIsRebuilding(false);
        console.log(res)
        getContainer();
      })
      .catch((e) => {
        setIsRebuilding(false);
        console.log(e)
        getContainer();
      });
  };


  const [analytics, setAnalytics] = React.useState([]);

  async function getAnalytics() {
    await axios.post(`${process.env.REACT_APP_BACKEND_URL}/dp/analytics-by-hostname`, {
      port: port,
    }).then((res) => {
      setAnalytics(res.data);
    }).catch((e) => {
      console.log(e)
    });
  }

  function AnylaticsWs() {
    const analytics_ws = `wss://${hostname}/dp/ws`;
    var any_ws = new WebSocket(analytics_ws);

    any_ws.onmessage = (e) => {
      if (e.data === null) return
      getAnalytics();
    }
  }


  const [stats, setStats] = React.useState([]);
  const [connected, setConnected] = React.useState(false)
  var cpu_usage = stats.cpu_usage / 1000000000 / 100

  //Function to connect to websocket
  function connect() {
    //On new message
    var wsUrl = `wss://${hostname}/v1/docker/container-stats/${container_id}`;
    var ws = new WebSocket(wsUrl);

    // ws.onopen = () => {
    //   setConnected(true)
    // };

    ws.onmessage = (event) => {
      if (event.data === null) return
      setStats(JSON.parse(event.data));
    }

    ws.onclose = () => {
      setConnected(false)
    };
  }


  React.useEffect(() => {
    getDomain();
    if (data && data.length > 0) {
      getContainer();
      connect();
    }
    getIP();
  }, []);


  //Use effect to connect to websocket
  React.useEffect(() => {
    getDomain();
  }, [domain]);

  React.useEffect(() => {
    getContainer();
    getAnalytics();
    AnylaticsWs();
  }, [container_id]);


  React.useEffect(() => {
    //On new message
    var wsUrl = `wss://${hostname}/v1/docker/container-stats/${container_id}`;
    var ws = new WebSocket(wsUrl);

    ws.onopen = () => {
      setConnected(true)
    };

    ws.onmessage = (event) => {
      if (event.data === null) return
      setStats(JSON.parse(event.data));
    }

    ws.onclose = () => {
      setConnected(false)
    };

    return () => ws.close();
  }, [container_id, domain]);




  const options = {
    readOnly: true
  };

  const [dns_model_open, setDnsModelOpen] = React.useState(false);
  const [dns_name, setDnsName] = React.useState("");
  const [is_dns_adding, setIsDnsAdding] = React.useState(false);
  const [is_dns_generating, setIsDnsGenerating] = React.useState(false);

  //Post request to add dns
  async function generateDNS() {
    var domain = data[0].length !== 0 ? data[0].name : "";
    //Split dot, underscore and replace with dash
    domain = domain.split('.').join('-').split('_').join('-');
    //Add Dome randome string to domain name
    var random = Math.random().toString(36).substring(7);
    var new_domain = `${domain}-${random}.dauqu.host`;

    setIsDnsGenerating(true);
    await axios.post(`${process.env.REACT_APP_BACKEND_URL}/v1/web/generate-domain`, {
      id: data[0]._id,
      domain: new_domain,
      port: data[0].bind_port,
      content: ip.ip
    }).then((res) => {
      setDnsModelOpen(false);
      setIsDnsGenerating(false);
      getDomain();
      toast(res.data.message, {
        type: "success",
      });
    }).catch((e) => {
      setDnsModelOpen(false);
      setIsDnsGenerating(false);
      toast(e.response.data.message, {
        type: "error",
      });
    })
  }

  //Post request to add dns
  async function addDNS() {

    setIsDnsAdding(true);
    await axios.post(`${process.env.REACT_APP_BACKEND_URL}/v1/web/add-domain`, {
      id: data[0]._id,
      domain: dns_name,
      port: data[0].bind_port,
      content: "34.70.146.178"
    }).then((res) => {
      setDnsModelOpen(false);
      setIsDnsAdding(false);
      toast(res.data.message, {
        type: "success",
      });
      getDomain();
    }).catch((e) => {
      setDnsModelOpen(false);
      setIsDnsAdding(false);
      toast(e.response.data.message, {
        type: "error",
      });
    })
  }

  //Delete dns
  async function deleteDNS(domain) {
    await axios.post(`${process.env.REACT_APP_BACKEND_URL}/v1/web/delete-domain`, {
      id: data && data.length > 0 ? data[0]._id : "",
      domain: domain,
    }).then((res) => {
      toast(res.data.message, {
        type: "success",
      });
      getDomain();
    }).catch((e) => {
      toast(e.response.data.message, {
        type: "error",
      });
    })
  }


  return (
    <div className="h-full w-full p-3 dark:bg-[#193549] bg-slate-200 overflow-y-scroll">
      <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800 text-white">
        <div className="dark:bg-slate-800 bg-slate-400 p-0 rounded-sm">
          <div className="bg-slate-100 dark:bg-transparent rounded-sm">
            <div className="flex justify-between items-center h-[45px] border-b px-5 bg-[#1E293B] w-auto">
              <div className="flex items-center">
                <div className={`w-[16px] h-[16px] rounded-full ${container_status === "running" ? "bg-green-500" : "bg-red-500"
                  } mr-3`}></div>
                <div className="dark:text-slate-200 text-white text-sm font-semibold uppercase">
                  {container_status}
                </div>
              </div>
              <div>
                <ul className="flex items-center">

                  {/* Restart */}
                  <li className=" dark:text-slate-200 text-white">
                    <label className={`btn btn-link no-underline dark:text-slate-200 text-white btn-sm font-normal ${isRestarting ? "loading" : ""}`} onClick={() => {
                      restartContainer();
                    }}>
                      {isRestarting ? "Restarting..." : "Restart"}
                    </label>
                  </li>


                  {/* Stop */}
                  <li className="">
                    <label htmlFor="action-modal" className={`btn btn-link no-underline dark:text-slate-200 text-white btn-sm font-normal ${isActionInProgress ? "loading" : ""}`}>
                      {container_status === "running" ? isActionInProgress ? "Stopping..." : "Stop" : isActionInProgress ? "Starting..." : "Start"}
                    </label>
                    <input type="checkbox" id="action-modal" className="modal-toggle" />
                    <div className="modal">
                      <div className="modal-box rounded-none dark:bg-gray-800 bg-gray-50">
                        <h3 className="font-bold text-lg dark:text-slate-200 text-slate-600">
                          Do you want to {container_status === "running" ? "stop" : "start"} this container?
                        </h3>
                        <p className="py-4 dark:text-slate-200 text-slate-600">
                          This action will {container_status === "running" ? "stop" : "start"} the container.
                        </p>
                        <div className="modal-action">
                          <label htmlFor="action-modal" className="btn btn-sm rounded-none">
                            Cancel
                          </label>
                          <button className={`btn btn-primary btn-error btn-sm rounded-none ${isActionInProgress ? "loading" : ""}`} onClick={
                            () => {
                              container_status === "running" ? stopContainer() : startContainer()
                              //Close modal
                              document.getElementById("action-modal").checked = false;
                            }
                          }>
                            {container_status === "running" ? isActionInProgress ? "Stopping..." : "Stop" : isActionInProgress ? "Starting..." : "Start"}
                          </button>
                        </div>
                      </div>
                    </div>
                  </li>


                  {/* Rebuild */}
                  <li className=" dark:text-slate-200 text-white">
                    <label className={`btn btn-link no-underline dark:text-slate-200 text-white btn-sm font-normal ${isRebuilding ? "loading" : ""}`} onClick={() => {
                      reBuild();
                    }}>
                      {isRebuilding ? "Rebuilding..." : "Rebuild"}
                    </label>
                  </li>

                  {/* Delete */}
                  <li className="">
                    <label htmlFor="my-modal" className="btn btn-link no-underline dark:text-slate-200 text-white btn-sm font-normal">
                      {isDeleting ? "Deleting..." : "Delete"}
                    </label>
                    <input type="checkbox" id="my-modal" className="modal-toggle" />
                    <div className="modal">
                      <div className="modal-box rounded-none dark:bg-gray-800 bg-gray-50">
                        <h3 className="font-bold text-lg dark:text-slate-200 text-slate-600">Do you really want to delete this website?</h3>
                        <p className="py-4 dark:text-slate-200 text-slate-600">
                          This action will delete the website and all the data associated with it.
                        </p>
                        <div className="modal-action">
                          <label htmlFor="my-modal" className="btn btn-sm rounded-none">
                            Cancel
                          </label>
                          <button className="btn btn-primary btn-error btn-sm rounded-none" onClick={
                            () => {
                              deleteDomain()
                              //Close modal
                              document.getElementById("my-modal").checked = false;
                            }
                          }>
                            Delete
                          </button>
                        </div>
                      </div>
                    </div>
                  </li>

                  {/* <li className="ml-3 dark:text-slate-200 text-black">
                    <a href="">
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-three-dots" viewBox="0 0 16 16">
                        <path d="M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z" />
                      </svg>
                    </a>
                  </li> */}
                </ul>
              </div>
            </div>

            <div className="p-2">
              <div className="grid gap-2 grid-cols-1 2xl:grid-cols-3 xl:grid-cols-3 lg:grid-cols-1 md:grid-cols-1 sm:grid-cols-1 select-none w-auto">
                <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-400 dark:bg-gray-800 mt-2 row-start-1 row-end-4 max-h-60">
                  <div className="dark:bg-slate-800 flex justify-center pl-2 h-full">
                    {/* CPU */}
                
                    <img src={`https://${hostname}/public/screenshots/${domain}.png`} alt="Screenshot" className="w-full h-full object-cover" onError={
                      (e) => {
                        e.target.onerror = null || "" || undefined;
                        e.target.src = "https://upload.wikimedia.org/wikipedia/commons/thumb/d/d1/Image_not_available.png/640px-Image_not_available.png"
                      }
                    } />
                  </div>
                </blockquote>

                <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-400 dark:bg-gray-800 mt-2 row-start-1 row-end-4">
                  <div className="dark:bg-slate-800 flex justify-center pl-2">
                    {/* Stats */}
                    <div className="stats stats-vertical w-full rounded-none bg-transparent">
                      <div className="stat w-full flex justify-between items-center p-2 pr-5">
                        <div className="justify-start text-start">
                          <div className="stat-title dark:text-slate-200 text-slate-600">
                            CPU Usage
                          </div>
                          <div className="stat-value text-xl w-28 dark:text-slate-200 text-slate-600">
                            {cpu_usage.toString().slice(0, 8)} %<span className="text-sm dark:text-slate-200 text-slate-600">/ 100%</span>
                          </div>
                        </div>
                        <div>
                          <div className="radial-progress dark:fill-slate-100 fill-slate-600" style={{ "--value": cpu_usage, "--size": "4rem", "--thickness": "1px" }}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" className="bi bi-cpu-fill" viewBox="0 0 16 16">
                              <path d="M6.5 6a.5.5 0 0 0-.5.5v3a.5.5 0 0 0 .5.5h3a.5.5 0 0 0 .5-.5v-3a.5.5 0 0 0-.5-.5h-3z" />
                              <path d="M5.5.5a.5.5 0 0 0-1 0V2A2.5 2.5 0 0 0 2 4.5H.5a.5.5 0 0 0 0 1H2v1H.5a.5.5 0 0 0 0 1H2v1H.5a.5.5 0 0 0 0 1H2v1H.5a.5.5 0 0 0 0 1H2A2.5 2.5 0 0 0 4.5 14v1.5a.5.5 0 0 0 1 0V14h1v1.5a.5.5 0 0 0 1 0V14h1v1.5a.5.5 0 0 0 1 0V14h1v1.5a.5.5 0 0 0 1 0V14a2.5 2.5 0 0 0 2.5-2.5h1.5a.5.5 0 0 0 0-1H14v-1h1.5a.5.5 0 0 0 0-1H14v-1h1.5a.5.5 0 0 0 0-1H14v-1h1.5a.5.5 0 0 0 0-1H14A2.5 2.5 0 0 0 11.5 2V.5a.5.5 0 0 0-1 0V2h-1V.5a.5.5 0 0 0-1 0V2h-1V.5a.5.5 0 0 0-1 0V2h-1V.5zm1 4.5h3A1.5 1.5 0 0 1 11 6.5v3A1.5 1.5 0 0 1 9.5 11h-3A1.5 1.5 0 0 1 5 9.5v-3A1.5 1.5 0 0 1 6.5 5z" />
                            </svg>
                          </div>
                        </div>
                      </div>

                      <div className="stat w-full flex justify-between items-center p-2 pr-5">
                        <div className="justify-start text-start">
                          <div className="stat-title dark:text-slate-200 text-slate-600">
                            Memory Usage
                          </div>
                          <div className="stat-value text-xl dark:text-slate-200 text-slate-600">
                            {(stats.memory_usage / 1048576).toFixed(2) + " MB"} <span className="text-sm dark:text-slate-200 text-slate-600"></span>
                          </div>
                        </div>
                        <div>
                          <div className="radial-progress" style={{ "--value": (stats.memory_usage / 1048576).toFixed(2), "--size": "4rem", "--thickness": "1px" }}>1 GB</div>
                        </div>
                      </div>

                      <div className="stat w-full flex justify-between items-center p-2 pr-5">
                        <div className="justify-start text-start">
                          <div className="stat-title dark:text-slate-200 text-slate-600">
                            Read / Write
                          </div>
                          <div className="stat-value text-xl dark:text-slate-200 text-slate-600">
                            {(stats.desk_read / 1048576).toFixed(2) + " MB"} <span className="text-sm dark:text-slate-200 text-slate-600">/ {(stats.desk_write / 1048576).toFixed(2) + " MB"}</span>
                          </div>
                        </div>
                        <div>
                          <div className="radial-progress" style={{ "--value": 100, "--size": "4rem", "--thickness": "1px" }}>MB</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </blockquote>

                {/* Right Side */}
                <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-400 dark:bg-gray-800 mt-2 row-start-1 row-end-4">
                  <div className="dark:bg-slate-800 flex pl-2 flex-col justify-between h-full">
                    {/* Stats */}
                    <div className="stats stats-vertical w-full rounded-none bg-transparent flex flex-col justify-between h-full">
                      {/* Traffic Anylatics */}
                      <div className="stat w-full flex justify-between items-center p-2 pr-5 h-full text-start">
                        <div>
                          <div className="stat-title dark:text-slate-200 text-slate-800">
                            Total Requests
                          </div>
                          <div className="stat-value text-xl w-28 dark:text-slate-200 text-slate-600">
                          </div>
                          <div className="stat-desc dark:text-slate-200 text-slate-800">
                            All Time Requests
                          </div>
                        </div>
                        <div>
                          {/* Number */}
                          <div className="stat-value text-xl dark:text-slate-200 text-slate-800">
                            {analytics.total_requests !== null && analytics.total_requests !== 0 ? analytics.total_requests : "0"}
                          </div>
                        </div>
                      </div>

                      {/* Uniqu View */}
                      <div className="stat w-full flex justify-between items-center p-2 pr-5 h-full text-left">
                        <div>
                          <div className="stat-title dark:text-slate-200 text-slate-600">
                            All Time Views
                          </div>

                          <div className="stat-desc dark:text-slate-200 text-slate-600">
                            Only hits from unique IPs
                          </div>
                        </div>
                        <div className="stat-value text-xl dark:text-slate-200 text-slate-600">
                          {analytics.unique_visitors !== null && analytics.unique_visitors !== 0 ? analytics.unique_visitors : "0"}
                        </div>
                      </div>

                      {/* Current USers */}
                      <div className="stat w-full flex justify-between items-center p-2 pr-5 h-full text-left">
                        <div>
                          <div className="stat-title dark:text-slate-200 text-slate-600">
                            Past 24 Hours
                          </div>
                          <div className="stat-desc dark:text-slate-200 text-slate-600">
                            Only hits from unique IPs
                          </div>
                        </div>
                        <div className="stat-value text-xl dark:text-slate-200 text-slate-600">
                          {analytics.last_24_hours !== null && analytics.last_24_hours !== 0 ? analytics.last_24_hours : "0"}
                        </div>
                      </div>

                    </div>
                  </div>
                </blockquote>
              </div>
            </div>
            <div className="flex items-center justify-between h-[50px] px-5">
              <div className="flex items-center">
                <div className="flex items-center">
                  <p className="text-sm dark:text-slate-200 text-slate-600">Name:&nbsp;</p>
                  <a className="text-sm dark:text-slate-200 text-slate-600">{data.length !== null && data.length !== 0 ? data[0].name : ""}</a>
                </div>
                <div className="h-[20px] w-[1px] bg-slate-200 mx-3"></div>
                <div className="flex items-center">
                  <p className="text-sm dark:text-slate-200 text-slate-600">Container ID:&nbsp;</p>
                  <p className="text-sm dark:text-slate-200 text-slate-600">{data.length !== null && data.length !== 0 ? data[0].container_id.slice(0, 15) : ""}</p>
                </div>
                <div className="h-[20px] w-[1px] bg-slate-200 mx-3"></div>
                <div className="flex items-center">
                  <p className="text-sm dark:text-slate-200 text-slate-600">Image:&nbsp;</p>
                  <p className="text-sm dark:text-slate-200 text-slate-600">{data.length !== null && data.length !== 0 ? data[0].image : ""}</p>
                </div>
                <div className="h-[20px] w-[1px] bg-slate-200 mx-3"></div>
                <div className="flex items-center">
                  <p className="text-sm dark:text-slate-200 text-slate-600">Created:&nbsp;</p>
                  <p className="text-sm dark:text-slate-200 text-slate-600">{data.length !== null && data.length !== 0 ? data[0].created_at : ""}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </blockquote>
      {/* Tabs */}
      <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800">
        <div className="mt-2 dark:bg-slate-800 bg-slate-400 rounded-sm">
          <div className="bg-slate-100 dark:bg-transparent p-0 rounded-sm">
            <div className="w-full">
              <div className="flex">
                <div className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] cursor-pointer ${tab === "details" ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm" : ""}`} onClick={() => { setTab("details") }}>Details</div>
                <div className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] user_grant_li cursor-pointer ${tab === "terminal" ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm" : ""}`} onClick={() => { setTab("terminal") }}>Terminal</div>
                <div className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] service_transfer_li cursor-pointer ${tab === "container-details" ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm" : ""}`} onClick={() => {
                  setTab("container-details")
                  getContainer()
                }}>Container Details</div>
                <div className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] maintenance_li cursor-pointer ${tab === "logs" ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm" : ""}`} onClick={() => {
                  setTab("logs")
                }}>Logs</div>
                <div className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] settings_li cursor-pointer ${tab === "domains" ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm" : ""}`} onClick={() => { setTab("domains") }}>Domains</div>
                <div className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] settings_li cursor-pointer ${tab === "settings" ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm" : ""}`} onClick={() => { setTab("settings") }}>Settings</div>
              </div>
            </div>
            {tab === "details" && (<div className="w-full">
              <div className="overflow-x-auto bg-white dark:bg-slate-800 p-2">
                <table className="table table-compact w-full rounded-none">
                  <thead className=" bg-slate-200 rounded-none">
                    <tr className="rounded-node bg-slate-100 dark:bg-slate-700">
                      <th className="rounded-node bg-transparent rounded-none"></th>
                      <th className="rounded-node bg-transparent rounded-none dark:text-slate-200 text-slate-600">Name</th>
                      <th className="rounded-node bg-transparent rounded-none dark:text-slate-200 text-slate-600">Details</th>
                      <th className="rounded-node bg-transparent rounded-none dark:text-slate-200 text-slate-600">Info</th>
                    </tr>
                  </thead>
                  <tbody className="bg-slate-200 rounded-none dark:bg-slate-800">
                    {/* Hostname */}
                    <tr className="rounded-none bg-white dark:bg-transparent">
                      <th className="bg-transparent dark:text-slate-200 text-slate-600">1</th>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">Hostnamet</td>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">
                        {data.length !== 0 ? data[0].name : ""}
                      </td>
                      <td className="cursor-pointer bg-transparent dark:text-slate-200 text-slate-600" onClick={() => {
                        // window.open("https://lpdc.dauqu.host")
                      }}><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-info-circle" viewBox="0 0 16 16">
                          <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
                          <path d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
                        </svg></td>
                    </tr>
                    {/* Port  */}
                    <tr className="rounded-none bg-white dark:bg-transparent">
                      <th className="bg-transparent dark:text-slate-200 text-slate-600">2</th>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">Port Bindings</td>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">
                        {data.length !== 0 ? data[0].exposed_port : ""}:{data.length !== 0 ? data[0].bind_port : ""}
                      </td>
                      <td className="cursor-pointer bg-transparent dark:text-slate-200 text-slate-600" onClick={() => {
                        // window.open("https://lpdc.dauqu.host")
                      }}><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-info-circle" viewBox="0 0 16 16">
                          <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
                          <path d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
                        </svg></td>
                    </tr>
                    {/* Deploy With */}
                    <tr className="rounded-none bg-white dark:bg-transparent">
                      <th className="bg-transparent dark:text-slate-200 text-slate-600">3</th>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">Deploy With</td>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">
                        {data.length !== 0 ? data[0].deploy_with.toUpperCase() : ""}
                      </td>
                      <td className="cursor-pointer bg-transparent dark:text-slate-200 text-slate-600" onClick={() => {
                        // window.open("https://lpdc.dauqu.host")
                      }}><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-info-circle" viewBox="0 0 16 16">
                          <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
                          <path d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
                        </svg></td>
                    </tr>

                    <tr className="rounded-none bg-white dark:bg-transparent">
                      <th className="bg-transparent dark:text-slate-200 text-slate-600">2</th>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">Deploy Source</td>
                      <td className=" bg-transparent dark:text-slate-200 text-slate-600">
                        {data.length !== 0 && data[0].deploy_source !== undefined && data[0].deploy_source !== null ? data[0].deploy_source : "Deploy Source Not Found"}
                      </td>
                      <td className="cursor-pointer bg-transparent dark:text-slate-200 text-slate-600" onClick={() => {
                        // window.open("https://lpdc.dauqu.host")
                      }}><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-info-circle" viewBox="0 0 16 16">
                          <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
                          <path d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
                        </svg></td>
                    </tr>

                  </tbody>
                </table>
              </div>
            </div>)}
            {tab === "terminal" && (<div className="w-auto mr-10">
              <div className="flex items-center justify-between p-1">
                <Editor
                  //Read only
                  options={options}
                  className="w-full h-[40vh]"
                  //Show value console style
                  //Dark
                  theme={localStorage.getItem("theme") === "dark" ? "vs-dark" : ""}
                  value="root@server:~#"
                />
              </div>
            </div>)}
            {tab === "container-details" && (<div className="w-auto h-auto overflow-scroll z-0">
              <div className="flex items-center justify-between p-1">
                <Editor
                  //Read only
                  options={options}
                  language="json"
                  theme={localStorage.getItem("theme") === "dark" ? "vs-dark" : ""}
                  className="w-full h-[40vh]"
                  value={container_details !== undefined || container_details !== null || container_details !== "" ? JSON.stringify(container_details, null, 2) : ""}
                />
              </div>
            </div>)}
            {tab === "logs" && (<div className="w-auto h-auto overflow-scroll z-0">

              <div className="flex items-center justify-between p-1">
                <ContainerLogs id={data[0].container_id} />
              </div>
            </div>)}
            {tab === "domains" && (<div className="w-full bg-white dark:bg-transparent">
              <div className="overflow-x-auto text-start p-2 dark:bg-transparent">
                <button className={`btn btn-sm rounded-none mb-5 mr-2 ${is_dns_generating ? "loading" : ""}`} onClick={() => { //get domain name from api
                  generateDNS();
                }}>
                  {is_dns_generating ? "Generating..." : "Generate Domain"}
                </button>
                <button className={`btn btn-sm rounded-none mb-5 ${is_dns_adding ? "loading" : ""}`} onClick={() => { setDnsModelOpen(true) }}>
                  {is_dns_adding ? "Adding..." : "Add Domain"}
                </button>
                <table className="table table-compact w-full rounded-none">
                  <thead className=" bg-slate-200 rounded-none">
                    <tr className="rounded-node bg-slate-100 dark:bg-slate-700">
                      <th className="rounded-node bg-transparent rounded-none"></th>
                      <th className="rounded-node bg-transparent rounded-none dark:text-slate-200 text-slate-600">Domain</th>
                      <th className="rounded-node bg-transparent rounded-none dark:text-slate-200 text-slate-600">Port</th>
                      <th className="rounded-node bg-transparent rounded-none dark:text-slate-200 text-slate-600">Created AT</th>
                      <th className="rounded-node bg-transparent rounded-none dark:text-slate-200 text-slate-600">Action</th>
                    </tr>
                  </thead>
                  {data[0].domains !== 0 && data[0].domains !== undefined ? data[0].domains.map((domain, index) => {
                    return (
                      <tbody className="bg-slate-200 rounded-none dark:bg-slate-800">
                        <tr className="rounded-none bg-white dark:bg-transparent">
                          <th className="bg-transparent text-slate-600">1</th>
                          <td className="bg-transparent dark:text-slate-200 text-slate-600 flex items-center cursor-pointer" onClick={() => {
                            window.open(`https://${data[0].domains.length !== 0 ? domain.domain : ""}`)
                          }}>https://{data[0].domains.length !== 0 ? domain.domain : ""} <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-box-arrow-in-up-right" viewBox="0 0 16 16">
                              <path fill-rule="evenodd" d="M6.364 13.5a.5.5 0 0 0 .5.5H13.5a1.5 1.5 0 0 0 1.5-1.5v-10A1.5 1.5 0 0 0 13.5 1h-10A1.5 1.5 0 0 0 2 2.5v6.636a.5.5 0 1 0 1 0V2.5a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 .5.5v10a.5.5 0 0 1-.5.5H6.864a.5.5 0 0 0-.5.5z" />
                              <path fill-rule="evenodd" d="M11 5.5a.5.5 0 0 0-.5-.5h-5a.5.5 0 0 0 0 1h3.793l-8.147 8.146a.5.5 0 0 0 .708.708L10 6.707V10.5a.5.5 0 0 0 1 0v-5z" />
                            </svg></td>
                          <td className=" bg-transparent dark:text-slate-200 text-slate-600">localhost:{data[0].domains.length !== 0 ? domain.port : ""}</td>
                          <td className="bg-transparent dark:text-slate-200 text-slate-600">
                            {data[0].domains.length !== 0 ? domain.created_at : ""}
                          </td>
                          <td className="bg-transparent dark:text-slate-200 text-slate-600">
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash-fill cursor-pointer" viewBox="0 0 16 16" onClick={
                              () => {
                                deleteDNS(domain.domain);
                              }
                            }>
                              <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z" />
                            </svg>
                          </td>
                        </tr>
                      </tbody>
                    )
                  }) : (
                    <div className="w-full bg-slate-200">
                      <p className="text-center text-slate-600">No Domains</p>
                    </div>
                  )}
                </table>
              </div>
            </div>)}
            {tab === "settings" && (<div className="w-full dark:bg-transparent p-2">Settings</div>)}
          </div>
        </div>
      </blockquote >


      {/* Create New Domain Model*/}
      <input type="checkbox" className="modal-toggle" checked={dns_model_open} />
      <div className="modal rounded-none">
        <div className="modal-box rounded-none bg-slate-50">
          <h3 className="font-bold text-lg text-start text-slate-600 dark:text-slate-200">
            Add your own domain
          </h3>
          <div className="mt-2 w-[100%]">
            <label className="label">
              <span className="label-text text-slate-600 dark:text-slate-200">Domain Name?</span>
            </label>
            <input type="text" placeholder="sub.example.com" className="input input-bordered w-full rounded-none input-sm dark:bg-slate-900 bg-slate-200 placeholder:dark:text-slate-200 placeholder:text-slate-600 dark:text-slate-200 text-slate-600" value={dns_name}
              onChange={(e) => {
                setDnsName(e.target.value);
              }} />
          </div>
          <div className="modal-action">
            <button className="btn rounded-none btn-sm btn-error" onClick={() => { setDnsModelOpen(false) }}>Cancel</button>
            <button className="btn rounded-none btn-sm" onClick={() => {
              addDNS();
            }}>Add Domain</button>
          </div>
        </div>
      </div>
    </div>
  );
}

