import axios from "axios";
import { Outlet } from "react-router-dom";
import { Link, useLocation } from "react-router-dom";
import * as React from "react";


export default function Websites() {
  const [allWebsites, setAllWebsites] = React.useState([]);

  //Axios GET request
  async function getWebsites() {
    await axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/v1/web/get-all`)
      .then((res) => {
        setAllWebsites(res.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }

  React.useEffect(() => {
    getWebsites();
  }, []);

  const location = useLocation();
  const path = location.pathname;

  return (
    <div className="flex-auto flex min-h-0 dark:bg-[#15232D] bg-slate-200">
      {/* Sidebar */}
      {/* Sec Sidebar */}
      <div className="hidden sm:flex flex-none border-r border-slate-500/30 flex-col items-center justify-between pb-4 w-56 pt-1 dark:bg-[#15232D] bg-slate-100">
        {/* Menus List */}
        <div className="pl-1 pr-1 pb-3 justify-start text-left w-full overflow-auto">
          {/* Top Action bar */}
          <div className="w-full flex p-1 cursor-pointer rounded-sm dark:bg-slate-900 bg-slate-800 mb-1 justify-between border-l-4 border-black">
            {/* Add New Website */}
            <h5 className="dark:text-slate-200 text-slate-200 ml-1">Websites</h5>
            <Link to="/ui/admin/web/create">
              <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" className="bi bi-plus fill-slate-300 mr-5" viewBox="0 0 16 16">
                <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z" />
              </svg>
            </Link>
            {/* <span className="ml-2 text-sm font-medium text-slate-400"></span> */}
          </div>
          {/* Menu Item */}
          {allWebsites === null ? (
            <div className="w-full flex p-1 cursor-pointer bg-slate-700 select-none rounded-sm">
              <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" className="bi bi-hash fill-slate-400 text-center justify-center select-none" viewBox="0 0 16 16">
              </svg>
              <h5 className="dark:text-slate-200 text-slate-200 ml-2 select-none">No Websites</h5>
            </div>
          ) : (
            allWebsites.map((item) => {
              return (
                <Link to={`/ui/admin/web/${item.name}`} key={item.name}>
                  <div
                    className={`w-full dark:hover:bg-slate-700 hover:bg-slate-300 flex p-1 cursor-pointer border-l-4 dark:border-black ${path === `/ui/admin/web/${item.name}`
                      ? "dark:bg-slate-700 bg-slate-300 select-none border-slate-500 dark:border-slate-300"
                      : ""
                      }`}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="18"
                      height="18"
                      className="bi bi-hash dark:fill-slate-200 fill-slate-800 select-none"
                      viewBox="0 0 16 16"
                    >
                      <path d="M8.39 12.648a1.32 1.32 0 0 0-.015.18c0 .305.21.508.5.508.266 0 .492-.172.555-.477l.554-2.703h1.204c.421 0 .617-.234.617-.547 0-.312-.188-.53-.617-.53h-.985l.516-2.524h1.265c.43 0 .618-.227.618-.547 0-.313-.188-.524-.618-.524h-1.046l.476-2.304a1.06 1.06 0 0 0 .016-.164.51.51 0 0 0-.516-.516.54.54 0 0 0-.539.43l-.523 2.554H7.617l.477-2.304c.008-.04.015-.118.015-.164a.512.512 0 0 0-.523-.516.539.539 0 0 0-.531.43L6.53 5.484H5.414c-.43 0-.617.22-.617.532 0 .312.187.539.617.539h.906l-.515 2.523H4.609c-.421 0-.609.219-.609.531 0 .313.188.547.61.547h.976l-.516 2.492c-.008.04-.015.125-.015.18 0 .305.21.508.5.508.265 0 .492-.172.554-.477l.555-2.703h2.242l-.515 2.492zm-1-6.109h2.266l-.515 2.563H6.859l.532-2.563z" />
                    </svg>
                    <span className="ml-2 text-sm dark:text-slate-200 text-slate-800 select-none">
                      {item.name}
                    </span>
                  </div>
                </Link>
              );
            }))}
        </div>
        {/* End Menus List */}
      </div>{" "}
      <div className="w-full h-full">
        <Outlet />
      </div>
    </div>
  );
}
