import { Outlet } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import Sidebar from "../components/sidebar";
import * as React from "react";
import axios from "axios";
import { toast } from 'react-toastify';

export default function Dashboard() {

  const navigate = useNavigate();

  const [nightMode, setNightMode] = React.useState(localStorage.getItem("theme"));


  const [isLoading, setIsLoading] = React.useState(false);
  const [isLogged, setIsLogged] = React.useState(true);

  async function checkLogin() {
    setIsLoading(true);
    await axios.get(`${process.env.REACT_APP_BACKEND_URL}/v1/auth/check-login`).then((res) => {
      setIsLoading(false);
      setIsLogged(res.data.isLogged);
    }).catch((err) => {
      console.log(err);
      setIsLoading(false);
    });
  }

  //Check login
  React.useEffect(() => {
    checkLogin();

    //Get theme from local storage and set it
    const theme = localStorage.getItem("theme");
    setNightMode(theme)
  }, []);

  //Manage night mode
  function manageNightMode() {
    if (nightMode) {
      localStorage.setItem("theme", "");
      setNightMode("");
    } else {
      localStorage.setItem("theme", "dark");
      setNightMode("dark");
    }

    //Reload page
    window.location.reload();
  }

  if (isLoading === false && isLogged === false) {
    navigate('/ui');
  }


    //Get Hostname from url
    var hostname = window.location.hostname;

    function AnylaticsWs() {
      const analytics_ws = `wss://${hostname}/dp/ws`;
      var any_ws = new WebSocket(analytics_ws);
  
      any_ws.onmessage = (e) => {
        if (e.data === null) return;
        let data = JSON.parse(e.data);
        //Show notification
        toast(`New ${data.method} Request on ${data.hostname}`, {
          type: "info",
        });
        //Play sound
        var audio = new Audio("https://www.soundjay.com/buttons/button-15.mp3");
        audio.play();
      };
    }


  //Restart panel 
  const [restartPanelLoading, setRestartPanelLoading] = React.useState(false);
  async function restartPanel() {
    setRestartPanelLoading(true);
    axios.get(`${process.env.REACT_APP_BACKEND_URL}/v1/proxy/restart`).then((res) => {
      toast(res.data.message, {
        type: "success",
      });
      setRestartPanelLoading(false);
    }).catch((err) => {
      toast(err.response.data.message, {
        type: "error",
      });
      setRestartPanelLoading(false);
    });
  }

  //Get IP Address
  const [ipAddress, setIpAddress] = React.useState("");
  async function getIpAddress() {
    await axios.get(`${process.env.REACT_APP_BACKEND_URL}/v1/system/get-ip`).then((res) => {
      setIpAddress(res.data);
    }).catch((err) => {
      console.log(err);
    });
  }

  //Get IP Address
  React.useEffect(() => {
    getIpAddress();
    AnylaticsWs();
  }, []);

  if (isLoading) {
    return (
      <div className="w-full h-screen flex justify-center items-center">
        <div className="radial-progress animate-spin" style={{ "--value": "70", "--size": "5rem", "--thickness": "2px" }}></div>
      </div>
    );
  } else {
    return (
      <div className={nightMode}>

        <div className="relative overflow-hidden shadow-xl flex max-h-schreen sm:max-h-[none] lg:h-screen xl:h-h-screen dark:bg-[#15232D] dark:backdrop-blur dark:ring-1 dark:ring-inset dark:ring-white/10 h-screen bg-slate-100">
          <div className="relative w-full flex flex-col">
            {/* Header */}
            <blockquote className="bg-gray-50 border-b-2 border-gray-400 dark:border-gray-700 dark:bg-gray-800 ">
              <div className="flex border-b border-slate-500/30 dark:bg-slate-900 bg-[#1E293B] justify-between pr-2">
                <div className="flex items-center h-8 space-x-1.5 px-3">
                  <div className="flex">

                  </div>

                  {/* Home Icons */}
                  <div className="flex items-center h-8 space-x-1.5 px-1">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="20"
                      height="20"
                      fill="currentColor"
                      className="bi bi-house-fill cursor-pointer fill-slate-100 dark:fill-slate-100"
                      viewBox="0 0 16 16"
                      onClick={() => {
                        navigate("/ui/admin");
                      }}
                    >
                      <path
                        fillRule="evenodd"
                        d="m8 3.293 6 6V13.5a1.5 1.5 0 0 1-1.5 1.5h-9A1.5 1.5 0 0 1 2 13.5V9.293l6-6zm5-.793V6l-2-2V2.5a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5z"
                      />
                      <path
                        fillRule="evenodd"
                        d="M7.293 1.5a1 1 0 0 1 1.414 0l6.647 6.646a.5.5 0 0 1-.708.708L8 2.207 1.354 8.854a.5.5 0 1 1-.708-.708L7.293 1.5z"
                      />
                    </svg>
                  </div>
                </div>

                {/* Restart panel */}
                <div className="flex items-center h-8 space-x-1.5 px-1">
                  <button className="hsf text-slate-200 dark:text-slate-100 hover:text-slate-100 dark:hover:text-slate-200 text-sm" onClick={() => {
                    restartPanel();
                  }}>
                    {restartPanelLoading ? "Restarting..." : "Restart Panel"}
                  </button>


                  {/* Dark Night */}
                  <div className="flex items-center h-8 space-x-1.5 px-1 w-[80px] justify-center">
                    <button className="hsf text-slate-200 dark:text-slate-100 hover:text-slate-100 dark:hover:text-slate-200 text-sm" onClick={manageNightMode}>
                      {nightMode === "dark" ? "Light" : "Dark"}
                    </button>
                  </div>
                </div>
              </div>
            </blockquote>
            <div className="relative min-h-0 flex-auto flex flex-col">
              <div className="flex-auto flex min-h-0">
                <Sidebar />
                {/* Body */}
                <Outlet />
              </div>

              {/* Footer */}
              <div className="border-t border-slate-500/30 font-mono text-xs leading-6 text-slate-200 p-4 space-y-2 dark:bg-slate-900 h-[50px] bg-[#1E293B]">
                <h3>
                  IP Address: {ipAddress.ip ? ipAddress.ip : "Loading..."}
                  <a
                    href="
                  "
                    target="_blank"
                    rel="noreferrer"
                    className="text-slate-200 hover:text-slate-100"
                  >
                    {" "}
                    {" "}
                  </a>
                </h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
