import VSEditor from "@monaco-editor/react";
import * as React from "react";
import axios from "axios";

export default function Editor() {

    const [fileData, setFileData] = React.useState("");
    const [document_root, setDocument_root] = React.useState("/");
    const [isLoading, setIsLoading] = React.useState(false);

    async function getFileData() {
        setIsLoading(true);
        await axios.post(`${process.env.REACT_APP_BACKEND_URL}/v1/files/read-file`, {
            Path: document_root,
        }).then((res) => {
            setFileData(res.data.data);
            setIsLoading(false);
        }).catch((e) => {
            console.log(e);
            setIsLoading(false);
        });
    }

    //Update File
    const [isUpdating, setIsUpdating] = React.useState(false);
    //Dectect changes in the file
    const [isChanged, setIsChanged] = React.useState(false);

    async function updateFile() {
        setIsUpdating(true);
        await axios.post(`${process.env.REACT_APP_BACKEND_URL}/v1/files/update-file`, {
            Path: document_root,
            Content: fileData,
        }).then((res) => {
            console.log(res);
            setIsUpdating(false);
            setIsChanged(false);
        }).catch((e) => {
            console.log(e);
            setIsUpdating(false);
        });
    }


    return (
        <div
            initial={{ opacity: 0, y: "5%" }}
            animate={{ y: "calc(10vw - 5%)" }}
            className="h-full w-full"
        >

            <div className="p-2 flex w-full h-full flex-col overflow-y-scroll dark:bg-[#193549] bg-slate-100 text-center align-center">
                <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800 items-center flex w-full">
                    <div className="flex w-full h-[40px] min-h-[40px] bg-[#1E293B] dark:bg-slate-900 text-white items-center justify-between">
                        <div className="flex ml-2 w-[30%] h-[30px]">
                            <input type="text" placeholder="Type here" className="input input-bordered w-full input-sm rounded-none"
                                onChange={(e) => {
                                    setDocument_root(e.target.value);
                                }}
                                value={document_root}/>
                            <button className="btn btn-sm rounded-none" onClick={() => {
                                getFileData();
                            }}>
                                {isLoading ? "Loading..." : "Read File"}
                            </button>
                        </div>

                        {/* Save Changes Button */}
                        <div className="flex ml-2 h-[30px] mr-5" onClick={() => {
                            updateFile();
                        }}>
                            <button className={`inline-flex btn btn-sm rounded-none items-center px-3 text-l text-gray-900 bg-gray-200 border border-r-0 border-gray-300 dark:bg-gray-600 dark:text-gray-100 dark:border-gray-600 cursor-pointer ${isChanged ? "bg-green-500 dark:bg-green-500" : "bg-gray-200 dark:bg-gray-600"
                                }`} onClick={() => {

                                }}>
                                {isUpdating ? "Updating..." : "Save Changes"}
                            </button>
                        </div>

                    </div>
                </blockquote>
                {/* Menu Bar */}
                <blockquote className="bg-white border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800 mt-2 p-2">
                    <div className="flex w-auto h-[80vh] min-h-[40px] bg-white dark:bg-slate-900 text-white text-center justify-between">
                        <VSEditor
                            className="w-auto h-full"
                            language="go"
                            theme={localStorage.getItem("theme") === "dark" ? "vs-dark" : "light"}
                            value={fileData !== "" ? fileData : ""}
                            onChange={(e) => {
                                setFileData(e);
                                setIsChanged(true);
                            }}
                        />
                    </div>
                </blockquote>
            </div>
        </div>
    );
}
