import * as React from "react";
import { motion } from "framer-motion";
import axios from "axios";
import { toast } from "react-toastify";

export default function DetailsDatabase() {
  const [check, setCheck] = React.useState([]);
  async function getMysql() {
    const response = await axios.get(
      `${process.env.REACT_APP_BACKEND_URL}/v1/databases/mysql/check`
    );
    setCheck(response.data);
  }

  const [database, setDatabase] = React.useState([]);
  const [gettingDatabase, setGettingDatabase] = React.useState(true);
  async function getDatabase() {
    setGettingDatabase(true);
    const response = await axios.get(
      `${process.env.REACT_APP_BACKEND_URL}/v1/databases/mysql/get-accounts`
    );
    setDatabase(response.data);
    setGettingDatabase(false);
  }

  const [hostname, setHostname] = React.useState([]);

  async function getHost() {
    const response = await axios.get(
      `${process.env.REACT_APP_BACKEND_URL}/v1/system/get-hostname`
    );
    setHostname(response.data);
  }

  React.useEffect(() => {
    getMysql();
    getDatabase();
    getHost();
  }, []);

  //Create Database
  const [databaseName, setDatabaseName] = React.useState("");
  const [username, setUsername] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [loading, setLoading] = React.useState(false);

  const [showAcountModal, setShowAcountModal] = React.useState(false);
  async function createDatabase() {
    setLoading(true);
    await axios
      .post(
        `${process.env.REACT_APP_BACKEND_URL}/v1/databases/mysql/create-account`,
        {
          Database: databaseName,
          Username: username,
          Password: password,
        }
      )
      .then((response) => {
        setShowAcountModal(false);
        setLoading(false);
        getDatabase();
        toast(response.data.message, {
          type: "success",
        });
      })
      .catch((error) => {
        setShowAcountModal(false);
        setLoading(false);
        toast(error.response.data.message, {
          type: "error",
        });
      });
  }

  const [showActionsModal, setShowActionsModal] = React.useState(false);

  const [isDeleting, setIsDeleting] = React.useState(false);
  const [deleteData, setDeleteData] = React.useState([]);

  async function deleteDatabase() {
    setIsDeleting(true);
    await axios
      .post(
        `${process.env.REACT_APP_BACKEND_URL}/v1/databases/mysql/delete-account`,
        {
          Database: deleteData.database,
          Username: deleteData.username,
        }
      )
      .then((response) => {
        console.log(response);
        setIsDeleting(false);
        setShowActionsModal(false);
        getDatabase();
        toast(response.data.message, {
          type: "success",
        });
      })
      .catch((error) => {
        console.log(error);
        setIsDeleting(false);
        setShowActionsModal(false);
        toast(error.response.data.message, {
          type: "error",
        });
      });
  }

  return (
    <div className="p-2 flex w-full h-full flex-col overflow-y-scroll dark:bg-[#1E293B]">
      {/* Model for Create Account */}
      <input
        type="checkbox"
        id="create-account-model"
        className="modal-toggle"
        checked={showAcountModal}
      />
      <div className="modal">
        <div className="modal-box rounded-none bg-white dark:bg-[#334154]">
          <h3 className="font-bold text-lg text-left text-slate-600 dark:text-slate-200">
            Create User
          </h3>
          <p className="py-4 text-left text-slate-600 dark:text-slate-200">
            Create a new user for <span className="text-red-500">MySQL</span>.
            Please fill the form below. All fields are required. this connection
            will be accessble from{" "}
            <span className="text-red-500">
              {hostname.hostname}:{"3306"}
            </span>
          </p>
          <div className="mt-2 w-[100%]">
            <label className="label">
              <span className="label-text text-slate-600 dark:text-slate-200">
                Your database name?
              </span>
              <span className="label-text-alt text-slate-600 dark:text-slate-200">
                Should be unique
              </span>
            </label>
            <input
              type="text"
              placeholder="Type here"
              className="input input-bordered w-full rounded-none input-sm"
              onChange={(e) => {
                setDatabaseName(e.target.value);
              }}
            />
          </div>
          <div className="mt-2 w-[100%]">
            <label className="label">
              <span className="label-text text-slate-600 dark:text-slate-200">
                Your username name?
              </span>
              <span className="label-text-alt text-slate-600 dark:text-slate-200">
                Should be unique
              </span>
            </label>
            <input
              type="text"
              placeholder="Type here"
              className="input input-bordered w-full rounded-none input-sm"
              onChange={(e) => {
                setUsername(e.target.value);
              }}
            />
          </div>
          <div className="mt-2 w-[100%]">
            <label className="label">
              <span className="label-text text-slate-600 dark:text-slate-200">
                Password?
              </span>
              <span className="label-text-alt text-slate-600 dark:text-slate-200">
                Keep it secure
              </span>
            </label>
            <input
              type="password"
              placeholder="Type here"
              className="input input-bordered w-full rounded-none input-sm"
              onChange={(e) => {
                setPassword(e.target.value);
              }}
            />
          </div>
          <div className="modal-action">
            <label
              className="btn btn-sm rounded-none"
              onClick={() => {
                setShowAcountModal(false);
              }}
            >
              Cancel{" "}
            </label>
            <button
              className={`btn btn-sm rounded-none ${loading ? "loading" : ""}`}
              onClick={() => {
                createDatabase();
              }}
            >
              {loading === true ? "Creating..." : "Create"}
            </button>
          </div>
        </div>
      </div>

      {/* Model for Create Account */}
      <input
        type="checkbox"
        id="action-model"
        className="modal-toggle"
        checked={showActionsModal}
      />
      <div className="modal">
        <div className="modal-box rounded-none bg-white dark:bg-[#334154]">
          <h3 className="font-bold text-lg text-left text-slate-600 dark:text-slate-200">
            Do you really want to delete database and user?
          </h3>
          <p className="py-4 text-left text-slate-600 dark:text-slate-200">
            This action will delete all data in this database. This action is
            irreversible.
          </p>
          <div className="modal-action">
            <button
              className="btn btn-sm rounded-none"
              onClick={() => {
                setShowActionsModal(false);
              }}
            >
              Cancel
            </button>
            <button
              className={`btn btn-error btn-sm rounded-none ${
                isDeleting ? "loading" : ""
              }`}
              onClick={() => {
                deleteDatabase();
              }}
            >
              {isDeleting === true ? "Deleting..." : "Delete"}
            </button>
          </div>
        </div>
      </div>

      {/* Top Bar */}
      <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800 mt-2">
        <div className="w-full h-[40px] dark:bg-slate-900 mb-0 flex bg-[#1E293B] rounded-sm justify-between items-center pl-2">
          <div className="flex items-center">
            <img
              src="https://cdn.worldvectorlogo.com/logos/mysql-6.svg"
              className="w-5 h-5 mr-5"
            />
            <div className="text-sm text-slate-200">MySQL</div>
            {/* Divider */}
            <div className="text-sm text-slate-200 ml-2">|</div>
            {/* Version */}
            <div className="text-sm text-slate-200 ml-2">v{check.version}</div>
            {/* Divider */}
            <div className="text-sm text-slate-200 ml-2">|</div>
            {/* Size in MB */}
            <div className="text-sm text-slate-200 ml-2">{check.size} MB</div>
          </div>
          <div>
            <ul className="flex items-center">
              <button
                onClick={() => {
                  setShowAcountModal(true);
                }}
                className="mx-3 text-sm text-slate-200 hover:underline cursor-pointer"
              >
                {loading ? "Creating..." : "Create Account"}
              </button>
            </ul>
          </div>
        </div>
      </blockquote>

      {/* Main */}
      <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800 mt-2">
        {gettingDatabase === false ? (
          <div className="dark:bg-slate-800 p-2 h-full">
            {check.isConnected === true ? (
              <div>
                <div className="flex-col w-full h-[5vh] flex justify-start text-left">
                  <h2 className="text dark:text-gray-400 text-black text-lg">
                    Available Databases on this server
                  </h2>
                </div>
                <div className="grid gap-1 grid-cols-2 2xl:grid-cols-6 xl:grid-cols-4 lg:grid-cols-4 md:grid-cols-3 sm:grid-cols-2 select-none dark:bg-slate-800 rounded-sm">
                  {database.length !== 0 &&
                    database.map((value) => (
                      <motion.div
                        key={value._id}
                        whileTap={{ scale: 0.9 }}
                        className="inline-flex justify-between items-center p-3 w-full border-l-4 border-gray-800 dark:bg-[#334154] mt-2 container z-40 overflow-hidden bg-slate-200 cursor-pointer"
                        onClick={() => {
                          setDeleteData(value);
                          setShowActionsModal(true);
                        }}
                      >
                        <div className="flex flex-col justify-center text-center">
                          <div className="flex justify-between w-auto">
                            <div
                              className="text-ellipsis overflow-hidden w-44 text-xs text-gray-500 dark:text-gray-400 text-left"
                              required=""
                            >
                              {/* {volume} */}
                            </div>
                          </div>
                          <div className=" text-md text-left text-ellipsis overflow-hidden w-44 text-slate-600 dark:text-slate-200">
                            {value.database}
                          </div>
                          {/* Divider */}
                          <div className="w-full h-0.5 mt-1 mb-2 bg-gray-600 dark:bg-gray-700"></div>
                          <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                            Connection URL:{" "}
                            {hostname.length !== 0 && hostname.hostname}:
                            {"3306"}
                          </div>

                          <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                            DatabaseName: {value.database}
                          </div>

                          <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                            Username: {value.username}
                          </div>

                          <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                            Password: {value.password}
                          </div>

                          {/* Size */}
                          {/* <div className="w-full text-md text-left text-sm mt-3">{volume}</div> */}
                          {/* <div className="w-full text-md text-left text-sm">{volume}</div> */}
                        </div>
                      </motion.div>
                    ))}
                </div>
              </div>
            ) : (
              <div>
                <div className="flex flex-col justify-center items-center w-full h-full">
                  Look like MySQL is not running on your system
                </div>

                {/* Divider */}
                <div className="w-full h-0.5 mt-1 mb-2 bg-gray-300 dark:bg-gray-700"></div>

                <button className="btn btn-sm rounded-none">
                  Check Problem
                </button>
              </div>
            )}
          </div>
        ) : (
          <div className="p p-10">
            <div
              className="radial-progress animate-spin"
              style={{
                "--value": "70",
                "--size": "5rem",
                "--thickness": "2px",
              }}
            ></div>
          </div>
        )}
      </blockquote>
    </div>
  );
}
