import axios from "axios";
import * as React from "react";
import moment from "moment";
import { motion } from "framer-motion";
import { toast } from "react-toastify";

export default function ViewVolumes() {
  const [volumes, setVolumes] = React.useState([]);
  const [containers, setContainers] = React.useState([]);
  const [images, setImages] = React.useState([]);

  //Get Volume details
  async function getVolumes() {
    const data = await axios.get(
      `${process.env.REACT_APP_BACKEND_URL}/v1/docker/all-volumes`
    );
    setVolumes(data.data.Volumes);
  }

  //Get Docker container details
  async function getContainers() {
    const data = await axios.get(
      `${process.env.REACT_APP_BACKEND_URL}/v1/docker/all-container`
    );
    setContainers(data.data.containers);
  }

  //Get Docker image
  async function getImages() {
    const data = await axios.get(
      `${process.env.REACT_APP_BACKEND_URL}/v1/docker/all-images`
    );
    setImages(data.data.images);
  }

  React.useEffect(() => {
    getImages();
    getContainers();
    getVolumes();
  }, []);

  //Tabs
  const [tab, setTab] = React.useState("containers");

  //Selected container
  const [selectedcontainer, setSelectedContainer] = React.useState({});
  const [selectedvolume, setSelectedVolume] = React.useState({});
  const [selectedimage, setSelectedImage] = React.useState({});

  //Delete container
  const [isContainerDeleting, setIsContainerDeleting] = React.useState(false);
  async function deleteContainer() {
    setIsContainerDeleting(true);
    await axios
      .delete(
        `${process.env.REACT_APP_BACKEND_URL}/v1/docker/delete-container/${selectedcontainer.Id}`
      )
      .then((res) => {
        setContainer_modal(false);
        setIsContainerDeleting(false);
        //Load containers
        getContainers();
        toast(`Container ${selectedcontainer.Names} deleted`, {
          type: "info",
        });
      })
      .catch((err) => {
        setContainer_modal(false);
        setIsContainerDeleting(false);
        toast(`Container ${selectedcontainer.Names} failed to delete`, {
          type: "error",
        });
      });
  }

  //Stop container
  const [isContainerStopping, setIsContainerStopping] = React.useState(false);

  async function stopContainerById() {
    setIsContainerStopping(true);
    await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/v1/docker/stop-container`, {
        ID: selectedcontainer.Id,
      })
      .then((res) => {
        setContainer_modal(false);
        setIsContainerStopping(false);
        getContainers();
        toast(`Container ${selectedcontainer.Names} stopped`, {
          type: "info",
        });
      })
      .catch((err) => {
        setContainer_modal(false);
        setIsContainerStopping(false);
        toast(`Container ${selectedcontainer.Names} failed to stop`, {
          type: "error",
        });
      });
  }

  //Start container
  const [isContainerStarting, setIsContainerStarting] = React.useState(false);
  async function startContainerById() {
    setIsContainerStarting(true);
    await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/v1/docker/start-container`, {
        ID: selectedcontainer.Id,
      })
      .then((res) => {
        setContainer_modal(false);
        setIsContainerStarting(false);
        //Load containers
        getContainers();
        toast(`Container ${selectedcontainer.Names} start`, {
          type: "info",
        });
      })
      .catch((err) => {
        setContainer_modal(false);
        setIsContainerStarting(false);
        toast(`Container ${selectedcontainer.Names} failed to start`, {
          type: "error",
        });
      });
  }

  //Delete image
  const [isImageDeleting, setIsImageDeleting] = React.useState(false);

  async function deleteImage() {
    //Remove sha256 from image id
    const e = selectedimage.Id.replace("sha256:", "");

    setIsImageDeleting(true);
    await axios
      .delete(
        `${process.env.REACT_APP_BACKEND_URL}/v1/docker/delete-image/${e}`
      )
      .then((res) => {
        setIsImageDeleting(false);
        //Load images
        getImages();
        setImage_modal(false);
        toast(`Image ${selectedimage.RepoTags[0]} deleted`, {
          type: "info",
        });
      })
      .catch((err) => {
        setIsImageDeleting(false);
        setImage_modal(false);
        toast(`Image ${selectedimage.RepoTags} failed to delete`, {
          type: "error",
        });
      });
  }

  //Delete volume
  const [isVolumeDeleting, setIsVolumeDeleting] = React.useState(false);
  async function deleteVolume() {
    setIsVolumeDeleting(true);
    await axios
      .delete(
        `${process.env.REACT_APP_BACKEND_URL}/v1/docker/delete-volume/${selectedvolume.Name}`
      )
      .then((res) => {
        setIsVolumeDeleting(false);
        //Load volumes
        getVolumes();
        setVolume_modal(false);
        toast(`Volume ${selectedvolume.Name} deleted`, {
          type: "info",
        });
      })
      .catch((err) => {
        setIsVolumeDeleting(false);
        setVolume_modal(false);
        toast(`Volume ${selectedvolume.Name} failed to delete`, {
          type: "error",
        });
      });
  }

  const [container_modal, setContainer_modal] = React.useState(false);
  const [image_modal, setImage_modal] = React.useState(false);
  const [volume_modal, setVolume_modal] = React.useState(false);

  return (
    <div className="p-2 flex w-full h-full flex-col dark:bg-[#193549]">
      <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800">
        <div className="w-full h-[40px] dark:bg-slate-900 mb-0 flex flex-col justify-center bg-[#1E293B] rounded-sm">
          {/* Path */}
          <div className="text-center flex dark:text-gray-200 text-slate-200 pl-2 justify-between">
            Docker
            <div>
              {/* Count Containers */}
              <button className="btn btn-xs rounded-none">
                Containers {containers.length}
              </button>
              <button className="btn btn-xs rounded-none">
                Images {images.length}
              </button>
              <button className="btn btn-xs rounded-none">
                Volumes {volumes.length}
              </button>
            </div>
          </div>
        </div>
      </blockquote>
      <div className="flex-col w-full h-auto flex justify-start text-left mt-1"></div>

      {/* CONTAINER MODEL */}
      <input
        type="checkbox"
        className="container-modal-toggle modal-toggle"
        checked={container_modal}
      />
      <div className="modal">
        <div className="modal-box rounded-none dark:bg-slate-700 bg-slate-100">
          <h3 className="font-bold text-lg text-left">
            <label
              className="font-bold text-lg text-left dark:text-slate-200 text-slate-600"
              for="container-modal-toggle"
            >
              Container Actions
            </label>
          </h3>
          <p className="py-4 text-left dark:text-slate-200 text-slate-600">
            Make sure you are aware of the actions you are taking.
          </p>
          <div className="modal-action">
            <button
              className="btn rounded-none btn-sm btn-error"
              onClick={() => {
                deleteContainer();
              }}
            >
              {isContainerDeleting ? "Deleting..." : "Delete"}
            </button>
            <button
              className="btn rounded-none btn-sm"
              onClick={() => {
                selectedcontainer.State === "running"
                  ? stopContainerById()
                  : startContainerById();
              }}
            >
              {selectedcontainer.State === "running"
                ? isContainerStopping
                  ? "Stopping..."
                  : "Stop"
                : isContainerStarting
                ? "Starting..."
                : "Start"}
            </button>
            <button
              className="btn rounded-none btn-sm"
              onClick={() => {
                setContainer_modal(false);
              }}
            >
              Cancel
            </button>
          </div>
        </div>
      </div>

      {/* IMAGE MODEL */}
      <input
        type="checkbox"
        className="image-modal-toggle modal-toggle"
        checked={image_modal}
      />
      <div className="modal">
        <div className="modal-box rounded-none dark:bg-slate-700 bg-slate-100">
          <h3 className="font-bold text-lg text-left">
            <label
              className="font-bold text-lg text-left dark:text-slate-200 text-slate-600"
              for="image-modal-toggle"
            >
              Image Actions
            </label>
          </h3>
          <p className="py-4 text-left dark:text-slate-200 text-slate-600">
            Make sure you are aware of the actions you are taking.
          </p>
          <div className="modal-action">
            <button
              className="btn rounded-none btn-sm btn-error"
              onClick={() => {
                deleteImage();
              }}
            >
              {isImageDeleting ? "Deleting..." : "Delete"}
            </button>
            <button
              className="btn rounded-none btn-sm"
              onClick={() => {
                setImage_modal(false);
              }}
            >
              Cancel
            </button>
          </div>
        </div>
      </div>

      {/* VOLUME MODEL */}
      <input
        type="checkbox"
        className="volume-modal-toggle modal-toggle"
        checked={volume_modal}
      />
      <div className="modal">
        <div className="modal-box rounded-none dark:bg-slate-700 bg-slate-100">
          <h3 className="font-bold text-lg text-left">
            <label
              className="font-bold text-lg text-left dark:text-slate-200 text-slate-600"
              for="volume-modal-toggle">
              Volume Actions
            </label>
          </h3>
          <p className="py-4 text-left dark:text-slate-200 text-slate-600">
            Make sure you are aware of the actions you are taking.
          </p>
          <div className="modal-action">
            <button
              className="btn rounded-none btn-sm btn-error"
              onClick={() => {
                deleteVolume();
              }}
            >
              {isVolumeDeleting ? "Deleting..." : "Delete"}
            </button>
            <button
              className="btn rounded-none btn-sm"
              onClick={() => {
                setVolume_modal(false);
              }}
            >
              Cancel
            </button>
          </div>
        </div>
      </div>

      <blockquote className="bg-gray-50 border-l-4 border-gray-800 dark:border-gray-500 dark:bg-gray-800 p-2 mb-2">
        <div className="mt-2 dark:bg-slate-800 bg-slate-400 rounded-sm">
          <div className="bg-slate-100 dark:bg-transparent p-0 rounded-sm">
            <div className="w-full">
              <div className="flex">
                <div
                  className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] cursor-pointer ${
                    tab === "containers"
                      ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm"
                      : ""
                  }`}
                  onClick={() => {
                    setTab("containers");
                  }}
                >
                  Containers
                </div>
                <div
                  className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] user_grant_li cursor-pointer ${
                    tab === "images"
                      ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm"
                      : ""
                  }`}
                  onClick={() => {
                    setTab("images");
                  }}
                >
                  Images
                </div>
                <div
                  className={`p-2 dark:text-slate-200 text-slate-600 font-medium text-[13px] service_transfer_li cursor-pointer ${
                    tab === "volumes"
                      ? "border-b-[2px] border-[#000000] dark:border-gray-500 dark:bg-slate-700 bg-slate-300 rounded-sm"
                      : ""
                  }`}
                  onClick={() => {
                    setTab("volumes");
                  }}
                >
                  Volumes
                </div>
              </div>
            </div>
            {tab === "containers" && (
              <div className="w-full">
                <div className="overflow-x-auto relative h-[75vh]">
                  <div className="grid gap-1 grid-cols-2 2xl:grid-cols-6 xl:grid-cols-6 lg:grid-cols-4 md:grid-cols-3 sm:grid-cols-2 select-none dark:bg-slate-800 p-1 rounded-sm">
                    {containers.length > 0 &&
                      containers.map((item, index) => (
                        <motion.div
                          whileTap={{ scale: 0.9 }}
                          className={`inline-flex justify-between items-center p-3 w-full border-l-4 border-gray-800 dark:bg-[#334154] mt-2 container z-40 overflow-hidden bg-slate-200 cursor-pointer ${
                            item.State === "exited" ||
                            item.State === "restarting"
                              ? "border-red-500"
                              : "border-green-500"
                          }`}
                          onClick={() => {
                            setSelectedContainer(item);
                            setContainer_modal(true);
                          }}
                        >
                          <div className="flex flex-col justify-center text-center">
                            <div className="flex justify-between w-auto">
                              <div
                                className="text-ellipsis overflow-hidden w-44 text-xs text-gray-500 dark:text-gray-400 text-left"
                                required=""
                              >
                                {/* {volume} */}
                              </div>
                            </div>
                            <div className=" text-md text-left text-ellipsis overflow-hidden w-44 text-slate-600 dark:text-slate-200">
                              {item.Names}
                            </div>
                            {/* Divider */}
                            <div className="w-full h-0.5 mt-1 mb-2 bg-gray-600 dark:bg-gray-700"></div>
                            <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                              <b>Image:</b> {item.Image}
                            </div>

                            <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                              <b>Status:</b> {item.Status}
                            </div>

                            <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                              <b>State:</b> {item.State}
                            </div>

                            <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                              <b>Created AT:</b>{" "}
                              {moment(item.Created * 1000).fromNow()}
                            </div>

                            {/* Size */}
                            {/* <div className="w-full text-md text-left text-sm mt-3">{volume}</div> */}
                            {/* <div className="w-full text-md text-left text-sm">{volume}</div> */}
                          </div>
                        </motion.div>
                      ))}
                  </div>
                </div>
              </div>
            )}
            {tab === "images" && (
              <div className="w-full">
                <div className="overflow-x-auto relative h-[75vh]">
                  <div className="grid gap-1 grid-cols-2 2xl:grid-cols-6 xl:grid-cols-6 lg:grid-cols-4 md:grid-cols-3 sm:grid-cols-2 select-none dark:bg-slate-800 p-1 rounded-sm">
                    {images.length > 0 &&
                      images !== null &&
                      images.map((item, index) => (
                        <motion.div
                          whileTap={{ scale: 0.9 }}
                          className="inline-flex justify-between items-center p-3 w-full border-l-4 border-gray-300 dark:bg-[#334154] mt-2 container z-40 overflow-hidden bg-slate-200 cursor-pointer"
                          onClick={() => {
                            setSelectedImage(item);
                            setImage_modal(true);
                          }}
                        >
                          <div className="flex flex-col justify-center text-center">
                            <div className="flex justify-between w-auto">
                              <div
                                className="text-ellipsis overflow-hidden w-44 text-xs text-gray-500 dark:text-gray-400 text-left"
                                required=""
                              >
                                {/* {volume} */}
                              </div>
                            </div>
                            <div className=" text-md text-left text-ellipsis overflow-hidden w-44 text-slate-600 dark:text-slate-200">
                              {item.RepoTags}
                            </div>
                            {/* Divider */}
                            <div className="w-full h-0.5 mt-1 mb-2 bg-gray-600 dark:bg-gray-700"></div>
                            <div className="text-xs text-left truncate text-slate-600 dark:text-slate-200 w-[200px] max-l">
                              <b>ID:</b> {item.Id}
                            </div>
                            <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                              <b>Virtual Size:</b> {item.VirtualSize / 1000000}{" "}
                              MB
                            </div>
                            <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                              <b>Size:</b> {item.Size / 1000000} MB
                            </div>
                            <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                              <b>Created AT:</b>{" "}
                              {moment(item.Created * 1000).fromNow()}
                            </div>

                            {/* Size */}
                            {/* <div className="w-full text-md text-left text-sm mt-3">{volume}</div> */}
                            {/* <div className="w-full text-md text-left text-sm">{volume}</div> */}
                          </div>
                        </motion.div>
                      ))}
                  </div>
                </div>
              </div>
            )}
            {tab === "volumes" && (
              <div className="w-full">
                <div className="overflow-x-auto relative h-[75vh]">
                  <table className="w-full text-sm text-left text-gray-500 dark:text-gray-400 mt-5">
                    <div className="grid gap-1 grid-cols-2 2xl:grid-cols-6 xl:grid-cols-6 lg:grid-cols-4 md:grid-cols-3 sm:grid-cols-2 select-none dark:bg-slate-800 p-1 rounded-sm">
                      {volumes.length > 0 &&
                        volumes.map((item, index) => (
                          <motion.div
                            whileTap={{ scale: 0.9 }}
                            className="inline-flex justify-between items-center p-3 w-full border-l-4 border-gray-300 dark:bg-[#334154] mt-2 container z-40 overflow-hidden bg-slate-200 cursor-pointer"
                            onClick={() => {
                              setSelectedVolume(item);
                              setVolume_modal(true);
                            }}
                          >
                            <div className="flex flex-col justify-center text-center">
                              <div className="flex justify-between w-auto">
                                <div
                                  className="text-ellipsis overflow-hidden w-44 text-xs text-gray-500 dark:text-gray-400 text-left"
                                  required=""
                                >
                                  {/* {volume} */}
                                </div>
                              </div>
                              <div className=" text-md text-left text-ellipsis overflow-hidden w-44 text-slate-600 dark:text-slate-200">
                                {item.Name}
                              </div>
                              {/* Divider */}
                              <div className="w-full h-0.5 mt-1 mb-2 bg-gray-600 dark:bg-gray-700"></div>
                              <div className="text-xs text-left truncate text-slate-600 dark:text-slate-200 w-[200px] max-l">
                                <b>Driver:</b> {item.Driver}
                              </div>
                              <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                                <b>Path:</b> {item.Mountpoint}
                              </div>
                              <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                                <b>Scope:</b> {item.Scope}
                              </div>
                              <div className="w-full text-xs overflow-ellipsis text-left truncate text-slate-600 dark:text-slate-200">
                                <b>Created AT:</b> {item.CreatedAt}
                              </div>
                              {/* Size */}
                              {/* <div className="w-full text-md text-left text-sm mt-3">{volume}</div> */}
                              {/* <div className="w-full text-md text-left text-sm">{volume}</div> */}
                            </div>
                          </motion.div>
                        ))}
                    </div>
                  </table>
                </div>
              </div>
            )}
          </div>
        </div>
      </blockquote>
    </div>
  );
}
