import "./App.css";
import * as React from "react";
import Dashboard from "./pages/dashboard";
import View from "./pages/files-pages/View";
import FileManager from "./pages/files";
import Website from "./pages/website";
import Domain from "./pages/websites/domain";
import Settings from "./pages/settings";
// import Profile from "./pages/Profile";
import Home from "./pages/Home";
import Upload from "./pages/files-pages/Upload";
import Databases from "./pages/Databases";
// import Extensions from "./pages/extensions";
import Welcome from "./pages/Welcome";
import Uploaded from "./pages/files-pages/Uploaded";
import AddDomain from "./pages/websites/Create";
import Webspaces from "./pages/files-pages/Webspaces";
import Terminal from "./pages/terminal/Termina";
// import FileEditor from "./pages/FileEditor";
import Technologies from "./pages/websites/Technologies";
import Searcha from "./pages/search/Search";
// import Emails from "./pages/Emails";
import DetailsDatabase from "./pages/databases/Details";
import ListedDb from "./pages/databases/Listed";
import ViewVolumes from "./pages/docker/View";
import Volumes from "./pages/Volumes";
import axios from "axios";
// import Template from "./pages/websites/Template";
import Editor from "./pages/files-pages/Editor";
import "react-toastify/dist/ReactToastify.css";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

axios.defaults.withCredentials = true;

function App() {
  const router = createBrowserRouter([
    {
      path: "/ui",
      element: <Welcome />,
    },
    {
      path: "/ui/admin",
      element: <Dashboard />,
      children: [
        {
          path: "",
          element: <Home />,
        },
        {
          path: "file-manager",
          element: <FileManager />,
          children: [
            {
              path: "",
              element: <Upload />,
            },
            {
              path: "system",
              element: <View />,
            },
            {
              path: "uploaded",
              element: <Uploaded />,
            },
            {
              path: "volumes",
              element: <Webspaces />,
            },
            {
              path: "file-editor",
              element: <Editor />,
            },
          ],
        },
        {
          path: "web",
          element: <Website />,
          children: [
            {
              path: ":domain",
              element: <Domain />,
            },
            {
              path: "create/:id",
              element: <AddDomain />,
            },
            {
              path: "create",
              element: <Technologies />,
            },
          ],
        },
        {
          path: "database",
          element: <Databases />,
          children: [
            {
              path: "",
              element: <ListedDb />,
            },
            {
              path: "details/:name",
              element: <DetailsDatabase />,
            },
            {
              path: "create",
              element: <Technologies />,
            },
          ],
        },
        {
          path: "docker",
          element: <Volumes />,
          children: [
            {
              path: "",
              element: <ViewVolumes />,
            },
          ],
        },
        {
          path: "terminal",
          element: <Terminal />,
        },
        {
          path: "settings",
          element: <Settings />,
        },
        {
          path: "search",
          element: <Searcha />,
        },
      ],
    },
  ]);

  const [isLoading, setIsLoading] = React.useState(true);

  React.useEffect(() => {
    // AnylaticsWs();
    axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/v1/auth/check-login`)
      .then((res) => {
        setIsLoading(false);
        if (res.data.isLogged === true) {
          //CHeck pathname is naot ui
          if (window.location.pathname === "/ui" || window.location.pathname === "/ui/") {
            window.location.href = "/ui/admin";
          } else {
            router.navigate(window.location.pathname);
          }
        }
      })
      .catch((err) => {
        setIsLoading(false);
      });
  }, []);

  if (isLoading) {
    return (
      <div className="w-full h-screen flex justify-center items-center">
        <div
          className="radial-progress animate-spin"
          style={{ "--value": "70", "--size": "5rem", "--thickness": "2px" }}
        ></div>
      </div>
    );
  } else {
    return (
      <div className="App">
        <ToastContainer
          position="bottom-right"
          autoClose={10000}
          hideProgressBar={false}
          newestOnTop
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
          theme={localStorage.getItem("theme") === "dark" ? "dark" : "light"}
        />
        <RouterProvider router={router} />
      </div>
    );
  }
}

export default App;
