import * as React from "react";
import Editor from "@monaco-editor/react";
export default function Terminal() {
  const [input, setInput] = React.useState("");
  const [socket, setSocket] = React.useState(null);
  const [output, setOutput] = React.useState("")


  const [connected, setConnected] = React.useState(false)

  var hostname = window.location.hostname;

  React.useEffect(() => {
    const ws = new WebSocket(`wss://${hostname}/v1/terminal/ws`);
    setSocket(ws);

    ws.onopen = () => {
      setConnected(true)
    };

    let allData = '';
    ws.onmessage = (event) => {
      const messages = event.data.split('\n');
      for (let i = 0; i < messages.length; i++) {
        allData += messages[i] + "\n";
      }
      setOutput(allData);
    };

    ws.onclose = () => {
      setConnected(false)
    };

  }, []);



  const handleSendMessage = () => {
    if (socket) {
      socket.send(input);
    }
  }

  const options = {
    readOnly: true
  };

  return (
    <div className="p-2 flex w-full h-auto flex-col overflow-scroll dark:bg-[#193549] justify-between">
      {/* Terminal area */}
      <div className="w-full h-[100vh] dark:bg-slate-700 bg-slate-300 p-0 justify-between flex flex-col border-l-4 border-slate-800">
        <div className="bg-white h-auto rounded-sm dark:bg-slate-800">

          <div className="w-full h-10 flex flex-col justify-center">
            <div className="w-full h-full flex justify-between items-center bg-[#1E293B] rounded-t-sm dark:bg-slate-900">
              {/* Title */}
              <h2 className="dark:text-gray-200 text-slate-200 text-lg ml-3">Terminal</h2>
              {/* Close button */}
              <div className={connected !== true ? "btn btn-sm btn-warning rounded-none mr-2" : "btn btn-sm btn-success rounded-none mr-2"}>
                {connected !== true ? "Closed" : "Connected"}
              </div>
            </div>
          </div>
        </div>
        <div className="justify-start items-start text-start w-full h-full overflow-y-scroll text-sm">
          {/* <pre>
            {output}
          </pre> */}
          <Editor
            className="w-full h-full"
            defaultLanguage="text"
            options={options}
            theme={localStorage.getItem("theme") === "dark" ? "vs-dark" : "vs-light"}
            value={output}
          />
        </div>
        <div className="h h-[4vh] w-full bg-white">
          {/* Terminal Input */}
          <div className="w-full h-auto flex justify-between items-center bg-[#1E293B] rounded-b-sm dark:bg-slate-900">
            <div className="h-8 bg-[#1E293B] flex justify-center items-center ml-3 cursor-pointer p-2 text-gray-100 dark:bg-slate-900">
              root@localhost:~#
            </div>
            <div className="w-full h-10 bg-slate-600 flex justify-center items-center ml-5">
              <input
                type="text"
                className="w-full h-full bg-[#1E293B] outline-none text-gray-100 dark:bg-slate-900 dark:text-white placeholder:text-white"
                placeholder="Type here..."
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyPress={(e) => {
                  if (e.key === "Enter") {
                    handleSendMessage();
                    setInput("");
                  }
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
