import Editor from "@monaco-editor/react";
import * as React from "react";
import axios from "axios";

export default function ContainerLogs({ id }) {

    const [logs, setLogs] = React.useState([]);
    //Convert to html 

    async function getContainerLogs() {
        await axios
            .get(`${process.env.REACT_APP_BACKEND_URL}/v1/docker/container-logs/${id}`,)
            .then((res) => {
                setLogs(res.data);
            })
            .catch((e) => {
            });
    }

    const options = {
        readOnly: true
    };

    React.useEffect(() => {
        getContainerLogs();
    }, []);

    return (
        <div className="w-full h-full">
            <Editor
                options={options}
                className="w-full h-[40vh]"
                value={logs.log}
                language="markdown"
                // Check class name is dark or light
                theme={localStorage.getItem("theme") === "dark" ? "vs-dark" : ""}
            />
        </div>
    )

}